  background-position: 0 0, 0 100%, 100% 0, 100% 100%;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  animation: rotate 1s linear infinite;
  position: relative;
}

@keyframes rotate {
  0% {
    width: 64px;
    height: 64px;
